.burger {
  padding: 0;
  margin-bottom: 7px;
  background-color: transparent;
  width: 26px;
  height: 20px;
  position: relative;
  z-index: 10;
  overflow: hidden;
  display: none;

  @include vp-767 {
    display: block;
  }

  span {
    width: 24px;
    height: 2px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: $color-alice-blue;
    transition: $trans-default;

    &:first-of-type {
      top: calc(50% - 5px);
    }

    &:last-of-type {
      top: calc(50% + 5px);
    }
  }

  &.no-js:hover,
  &.no-js:focus-within,
  &.is-active {
    span {
      top: 50%;
      background-color: $color-tangaroa;
      transform: translate(-50%, 0) rotate(45deg);
      width: 20px;

      &:first-of-type {
        display: none;
      }

      &:last-of-type {
        transform: translate(-50%, 0%) rotate(-45deg);
      }
    }
  }
}
